
.top-tip {
  position: fixed;
  top: 0;
  width: 100%;
  z-index: 500;
  background: #666;
}
.top-tip.drop-enter-active,
.top-tip.drop-leave-active {
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
}
.top-tip.drop-enter,
.top-tip.drop-leave-to {
  -webkit-transform: translate3d(0, -100%, 0);
          transform: translate3d(0, -100%, 0);
}


