  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  padding: 8px;
  text-align: center;
  font-size: 14px;
  color: rgba(255,255,255,0.3);
}
.switches .switch-item.active {
  background: #333;
  color: #fff;
}


